import React, {useEffect, useState} from "react";
import {Formik, Field, Form} from 'formik';
import './FormRegistration.css';
import {useNavigate} from "react-router-dom";
import {database, set, onValue, ref} from "../../../../util/firebase";
import PopUp from "../../../../components/PopUp";

const FormRegistration = () => {
  const navigate = useNavigate();
  const [oldDataMails, setOldDataMails] = useState([]);
  const [oldUsersAll, setOldUsersAll] = useState([]);
  const [oldUsersInfo, setOldUsersInfo] = useState([]);
  const [isVisibleError, setVisibleError] = useState(false);
  const [ErrorText, setErrorText] = useState("");

  useEffect(() => { //  c 
    onValue(ref(database, 'users/'), snapshot => {
      if (snapshot.val() !== null) setOldDataMails(Object.values(snapshot.val()));
    });
  }, []);

  useEffect(() => { //     . 
    onValue(ref(database, 'usersInfo/'), snapshot => {
      if (snapshot.val() !== null) setOldUsersInfo(Object.values(snapshot.val()));
    });
  }, []);

  useEffect(() => { //     +
    onValue(ref(database, 'usersAndPass/'), snapshot => {
      if (snapshot.val() !== null) setOldUsersAll(Object.values(snapshot.val()));
    });
  }, []);


  function createAcc(values) {
    if (values.email !== "" && values.pass !== "") { //    
      if (!Object.values(oldDataMails).includes(values.email)) { //      
        oldDataMails.push(values.email)
        oldUsersAll.push(values);
        oldUsersInfo.push({formEmail: values.email, formFirstName: "", formSecondName: "", formPhone: ""});
        set(ref(database, 'users/'), oldDataMails); //    
        set(ref(database, 'usersAndPass/'), oldUsersAll) //    
        set(ref(database, 'usersInfo/'), oldUsersInfo) //    . 
        localStorage.setItem('currentUser', JSON.stringify(values.email)); //        
        navigate('/'); // 
      } else {
        setErrorText("   ");
        setVisibleError(true);
      }
    } else {
      setErrorText("  ");
      setVisibleError(true);
    }
  }

  return (
    <div className="FormRegistration__container">
      {isVisibleError && <PopUp callClose={setVisibleError} message={ErrorText} title=""/>}
      <div className="FormRegistrationCenter">
        <p className="FormRegistration__largeText"></p>
        <div className="FormRegistration__textWithLink">
          <p className="textWithLink__first">  ?</p>
          <button onClick={() => navigate('/login')} className="textWithLink__second"></button>
        </div>
        <div>
          <Formik
            initialValues={{
              email: '',
              pass: '',
            }}
            onSubmit={async (values) => {
              await new Promise((r) => setTimeout(r, 400));
              createAcc(values);
            }}
          >
            <Form className="FormRegistration__inside">
              <label className="Formik__label" htmlFor="email">. </label>
              <Field
                className="Formik__input"
                id="email"
                name="email"
                type="email"
              />

              <label className="Formik__label" htmlFor="pass"></label>
              <Field
                className="Formik__input"
                id="pass"
                name="pass"
                placeholder=""
                type="password"
              />

              <button className="FormRegistration__button" type="submit"></button>
            </Form>
          </Formik>
        </div>
      </div>
    </div>
  )
}

export default FormRegistration;
