import React, {useState} from "react";
import './FormContact.css'
import {Field, Form, Formik} from "formik";
import {ref, push, database} from "../../../../util/firebase";
import PopUp from "../../../../components/PopUp";

const FormContact = () => {
  const [isVisibleError, setVisibleError] = useState(false);
  const [ErrorText, setErrorText] = useState("");

  function sendForm(values) {
    if (values.email !== "" && values.message !== "") {
      push(ref(database, 'messages/'), values)
      setErrorText(" ");
      setVisibleError(true);
    } else {
      setErrorText("   ");
      setVisibleError(true);
    }
  }

  return (
    <>
      {isVisibleError && <PopUp callClose={setVisibleError} message={ErrorText} title="" />}
      <div className="formContact">
        <p className="formContact__title">  </p>
        <div>
          <Formik
            initialValues={{
              firstName: '',
              secondName: '',
              email: '',
              message: '',
            }}
            onSubmit={async (values) => {
              await new Promise((r) => setTimeout(r, 400));
              sendForm(values);
            }}
          >
            <Form className="FormContact__inside">
              <label className="FormikContact__label" htmlFor="firstName"></label>
              <Field
                className="FormikContact__input"
                id="firstName"
                name="firstName"
                type="firstName"
              />

              <label className="FormikContact__label" htmlFor="secondName"></label>
              <Field
                className="FormikContact__input"
                id="secondName"
                name="secondName"
                type="secondName"
              />

              <label className="FormikContact__label" htmlFor="email">.  *</label>
              <Field
                className="FormikContact__input"
                id="email"
                name="email"
                type="email"
              />

              <label className="FormikContact__label" htmlFor="message">  *</label>
              <Field
                component="textarea"
                rows="3"
                className="FormikContact__input--large"
                id="message"
                name="message"
                placeholder=""
                type="message"
              />

              <button className="FormContact__button" type="submit"></button>
            </Form>
          </Formik>
        </div>
      </div>
    </>
  )
}

export default FormContact;
