import React, {useState} from "react";
import styles from './CartList.module.css'
import {useNavigate} from "react-router-dom";
import PopUp from "../../../../components/PopUp";

const CartList = () => {
  const [cartList] = useState(JSON.parse(localStorage.getItem('cart')));
  const [sum, setSum] = useState(() => {
    let temp = 0;
    for (let key in cartList) temp += (cartList[key].price * cartList[key].quantity);
    return temp;
  });
  const [isVisibleError, setVisibleError] = useState(false);
  const navigate = useNavigate()

  function changeSum() {
    let temp = 0;
    for (let key in cartList) temp += (cartList[key].price * cartList[key].quantity);
    setSum(temp);
  }

  function incCounter(item) {
    item.quantity++;
    localStorage.setItem("cart", JSON.stringify(cartList)) //  
    changeSum()
  }

  function decCounter(item) {
    if (item.quantity === 1) return 0 //   = 1,   
    item.quantity--;
    localStorage.setItem("cart", JSON.stringify(cartList)) //  
    changeSum()
  }

  function removeItem(item) {
    cartList.splice(cartList.indexOf(item), 1);
    localStorage.setItem("cart", JSON.stringify(cartList));
    changeSum();
  }

  function createOrder() {
    if (localStorage.getItem("currentUser") !== null) { //   
      navigate('/order')
    } else setVisibleError(true)
  }

  const res = cartList.map(item =>
    <div key={item.id} className={styles.card}>
      <img src={`/img/Catalog/${item?.img[0]}.png`} className={styles.cardImg} alt=""/>
      <div className={styles.cardRight}>
        <div className={styles.cardTop}>
          <p className={styles.cardName}>{item.name}</p>
          <div className={styles.cardTopRight}>
            <div className={styles.countContainer}>
              <button onClick={() => decCounter(item)} className={styles.countButtonFirst}>-</button>
              <p className={styles.countText}>{cartList[cartList.indexOf(item)].quantity}</p>
              <button onClick={() => incCounter(item)} className={styles.countButtonSecond}>+</button>
            </div>
            <p className={styles.cardPriceAll}>{item.price * item.quantity},00.</p>
            <input
              className={styles.crossBlack}
              alt=""
              src="/img/close-cross.jpg"
              type="image"
              onClick={() => removeItem(item)}
            />
          </div>
        </div>
        <p className={styles.cardPrice}>{item.price},00.</p>
      </div>
    </div>
  )

  return (
    <div className={styles.cartList}>
      {isVisibleError && <PopUp callClose={setVisibleError} message={"   "} title={""} />}
      <div className={styles.leftBlock}>
        <p className={styles.textTop}> </p>
        {res}
      </div>
      <div className={styles.rightBlock}>
        <p className={styles.textTop}> </p>
        <div className={styles.section}>
          <p className={styles.textSum}></p>
          <p className={styles.textSum}>{sum},00.</p>
        </div>
        <div className={styles.section}>
          <p className={styles.textDelivery}></p>
          <p className={styles.textDelivery}></p>
        </div>
        <p className={styles.textBelarus}></p>
        <select className={styles.select}>
          <option>  -   , , </option>
          <option>  - 3,00 .</option>
        </select>
        <div className={styles.sectionLine}>
          <p className={styles.total}></p>
          <p className={styles.total}>{sum},00 .</p>
        </div>
        <button onClick={() => createOrder()} className={styles.button}> </button>
        <div className={styles.lastLine}>
          <img className={styles.imgKey} src="/img/key.jpg" alt=""/>
          <p> </p>
        </div>
      </div>
    </div>
  )
}

export default CartList;
